.liga_desliga_checkbox {
    position: absolute;
    left: -9999px;
}

.liga_desliga_botao {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    cursor: pointer;
}

.liga_desliga_botao::before,
.liga_desliga_botao::after
{
    content: "";
    transition: all 0.3s ease;
}

.liga_desliga_botao::before{
    display: inline-block;
    width: 8em;
    height: 4em;
    background: #900;
    border-radius: 4em;
}

.liga_desliga_botao::after{
    position: absolute;
    top: 0.1em;
    left: 0.1em;
    width: 3.8em;
    height: 3.8em;
    background: #FFF;
    border-radius: 100%;
}

.liga_desliga_checkbox:checked + .liga_desliga_botao::before {
    background: #55d069;
}

.liga_desliga_checkbox:checked + .liga_desliga_botao::after {
    left: 4.1em;
}